
import { BookLock } from "lucide-react";
const item = [
    {
        title: 'Reports',
        icon: BookLock,
    },
    {
        title: 'Administration',
        icon: BookLock,
    },
    {
        title: 'Personnel',
        icon: BookLock,
    },
    {
        title: 'Safety',
        icon: BookLock,
    },
    {
        title: 'Ecology',
        icon: BookLock,
    }
]
const NavBar = () => {
    return (
        <div className="bg-red-400 w-[80vw] ml-30">
            <nav>
                <h1>Reconville</h1>
                {
                     item.map((items) =>(
                        <button>
                            <items.icon/>
                            <h1>{items.title}</h1>
                        </button>
                     ))   
                }
            </nav>
        </div>
    );
}

export default NavBar;